.user-navigation {
    list-style: none;
    margin: 0;
    padding: 0;

    font-size: 17px;
    line-height: 24px;
    font-weight: 700;
    color: #000000;
}

.user-navigation__link {
    font-size: 17px;
    line-height: 24px;
    font-weight: 700;
    color: #000000;
    text-transform: none;
    text-align: left;
    
    border: none;
    height: 79px;
    
    width: 100%;
    padding: 27px 10px;
    padding-left: 46px;

    position: relative;

    &::before {
        content: "";

        position: absolute;
        width: 320px;
        height: 1px;
        top: 0;
        left: -30px;
        background-color: #ececec;
    }

    &:hover,
    &:focus {
        border: none;
        background-color: transparent;
        color: #000000;
        opacity: 0.6;
    }

    &:active {
        opacity: 0.3;
    }
}

.user-navigation__link--search {
    
    &::after {
        content: "";
        position: absolute;

        width: 25px;
        height: 25px;
        background-color: transparent;

        background-image: url("../../img/icons/icon-search.svg");
        background-repeat: no-repeat;
        left: 2px;
        top: 32px;

        opacity: 0.2;
    }
}

.user-navigation__link--cart {
    
    &::after {
        content: "";
        position: absolute;

        width: 21px;
        height: 18px;
        background-color: transparent;

        background-image: url("../../img/icons/icon-cart.svg");
        background-repeat: no-repeat;
        left: 1px;
        top: 31px;

        opacity: 0.2;
    }
}

@media (min-width: 768px) {
    .user-navigation {
        display: flex;
        width: 500px;
        margin-left: auto;
    }

    .user-navigation__link-text {
        display: none;
    }
    
    .user-navigation__item {
        min-height: 89px;
    }

    .user-navigation__item--search {
        margin-left: auto;
        min-height: 89px;

    }

    .user-navigation__link {
        height: 89px;
    }

    .user-navigation__link--search {
        width: 83px;

        border-left: 1px solid #ececec;

        &::after {
        top: 36px;  
        left: 32px; 
    }
}

    .user-navigation__link--cart {
        width: 265px;
        padding-right: 0;
        padding-left: 82px;
        padding-top: 30px;

        border-left: 1px solid #ececec;

        &::after {
            top: 35px;
            left: 32px;
        }
    }
}

@media (min-width: 1050px) {
    .user-navigation {
        order: 3;
        box-sizing: border-box;
        margin: 0; 
        width: 500px; 
        padding-right: 50px;

    }
}
